    .list {
      width: 47%;
      display: flex;
      text-align: center;
      line-height: 3em;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      .title {
        font-weight: 500;
        letter-spacing: 10px;
        font-size: 34px;
        margin: 10px 0 8px;
      }
      .detail {
        color: #333;
        font-size: 20px;
        letter-spacing: 6px;
      }
      .btn-color {
        margin-top: 33px;
        background: linear-gradient(to left, #eb6140, #e2335b);
        border-radius: 100px;
        color: #fff;
        display: inline-flex;
        width: 150px;
        user-select: none;
        height: 53px;
        font-size: 20px;
        justify-content: center;
        cursor: pointer;
        align-items: center;
      }
    }
  }

  .section-three {
    width: 100%;
    background-color: #fafafa;
    padding: 30px 0;

    .container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      margin: auto;
      max-width: 1200px;

      .eng-banner {
        color: #ddd;
        display: inline-block;
        font-size: 42px;
        letter-spacing: 3px;
        font-weight: bold;
      }
      .zh-banner {
        font-size: 35px;
        letter-spacing: 0.5em;
        margin-top: 5px;
        display: inline-block;
        letter-spacing: 8px;
        font-size: 40px;
        font-weight: 500;
      }

      p.content {
        line-height: 1.8em;
        font-size: 18px;
        letter-spacing: 1px;
        margin: 22px 0;
        font-weight: 500;
      }

      .introduction-container {
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;

        .list {
          flex: 0 0 44%;
          margin: 10px 0 50px;
          height: 360px;
          display: flex;
          align-items: flex-end;
          background-size: contain;
          justify-content: flex-start;
          background-repeat: no-repeat;
          background-position: center 0%;
          // cursor: pointer;

          &:nth-child(1) {
            background-image: url("https://assets.vmei.com/system/cosmos/images/newCosmos/section-2-1.jpg");
          }
          &:nth-child(2) {
            background-image: url("https://assets.vmei.com/system/cosmos/images/newCosmos/section-2-2.jpg");
          }
          &:nth-child(3) {
            background-image: url("https://assets.vmei.com/system/cosmos/images/newCosmos/section-2-3.jpg");
          }
          &:nth-child(4) {
            background-image: url("https://assets.vmei.com/system/cosmos/images/newCosmos/section-2-4.jpg");
          }

          &:nth-child(2),
          &:nth-child(4) {
            justify-content: flex-end;
          }
          .context {
            background: #fff;
            width: 100%;
            padding: 20px 40px;
            height: 100%;
            max-height: 180px;
            transition: 0.3s all ease-in-out;
            overflow: hidden;
            .context-title {
              color: #e33858;
              font-size: 26px;
              letter-spacing: 3px;
              margin: 20px 0;
              font-weight: bold;
            }
            .context-content {
              min-height: 42px;
              letter-spacing: 2px;
            }
            p + p {
              margin-top: 30px;
            }
          }
        }
      }
    }
  }

  .section-four {
    position: relative;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto 170px;
    height: 660px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    text-align: center;

    .eng-banner {
      color: #ddd;
      display: inline-block;
      font-weight: 600;
      font-size: 40px;
      letter-spacing: 5px;
    }
    .zh-banner {
      margin-top: 20px;
      letter-spacing: 0.5em;
      display: inline-block;
      letter-spacing: 8px;
      font-size: 35px;
    }

    .advance-container {
      display: flex;
      flex-direction: row;
      width: 100%;
      flex-wrap: wrap;
      justify-content: space-between;

      .list {
        width: 500px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        text-align: justify;
        margin: 60px 0 0;

        .title {
          width: 100%;
          color: #e12c5f;
          margin: 30px 0;
          display: inline-flex;
          font-size: 35px;
          text-align: left;
          align-items: center;
          letter-spacing: 2px;
          justify-content: flex-start;

          .icon {
            flex: 0 0 50px;
            font-size: 38px;
            margin-right: 5px;
          }
        }
        .content {
          text-align: left;
          line-height: 1.8em;
          font-size: 18px;
          letter-spacing: 1px;
          font-weight: 500;
        }
      }
    }
  }

  .section-five {
    position: relative;
    margin: 70px 0 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .eng-banner {
      color: #ddd;
      display: inline-block;
      font-weight: 600;
      font-size: 40px;
      letter-spacing: 5px;
    }
    .zh-banner {
      margin-top: 20px;
      letter-spacing: 0.5em;
      display: inline-block;
      letter-spacing: 8px;
      font-size: 40px;
    }

    .contaienr {
      max-width: 1200px;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 100px;

      .left {
        flex: 0 0 44%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        background-image: url(https://static.pipk.top/api/public/images/3830265227150125.jpeg);
        background-size: contain;
        background-position: center;
        filter: brightness(0.9) contrast(90%);
        transition: 0.1s all ease-in-out;
        .btn-start {
          cursor: pointer;
          display: inline-flex;
          color: #fff;
          background: rgba(0, 0, 0, 0.8);
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 130px;
          height: 45px;
          font-size: 17px;
          border-radius: 100px;

          svg.icon {
            margin-right: 10px;
            font-size: 25px;
          }
        }
        video {
          width: 100%;
          height: 100%;
        }
        &:hover {
          filter: none;
        }
      }
      .right {
        flex: 0 0 44%;
        text-align: left;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .title {
          color: #e33858;
          font-size: 35px;
          font-weight: 500;
        }
        .main-content {
          font-size: 24px;
          margin-bottom: 25px;
          margin-top: 5px;
          letter-spacing: 2px;
        }
        .content {
          margin-bottom: 5px;
        }
        .btn-group {
          width: 100%;
          margin-top: 50px;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          button {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 150px;
            height: 50px;
            font-size: 16px;
            border-radius: 5px;
            background: #fff;
            border: none;
            cursor: pointer;
          }
          button:nth-child(1) {
            color: #fff;
            background: #e7567e;
          }
          button:nth-child(2) {
            border: 2px solid #e7567e;
            color: #e7567e;
          }
        }
      }
    }
  }

  .section-six {
    width: 100%;
    background-color: #f5f3f2;
    padding: 30px 0;

    .container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      margin: auto;
      max-width: 1200px;

      .zh-banner {
        font-size: 35px;
        margin-top: 5px;
        display: inline-block;
        font-size: 40px;
        font-weight: 500;
      }

      .introduction-container {
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;

        .list {
          padding: 16px;
          flex: 0 0 200px;
          margin: 30px 0 50px;
          background: #fff;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: center;
          height: 215px;

          img {
            width: 59%;
            margin: 20px 33px 8px;
          }

          .collaboration {
            color: #e33858;
            font-size: 125px;
            transform: scale(1.2);
            transform-origin: 50% 18%;
          }

          .edit {
            color: #e33858;
            margin: 20px 33px 8px;
            font-size: 100px;
          }

          .context-title {
            font-size: 16px;
          }

          .context-content {
            font-size: 12px;
          }
        }
      }
    }
  }

  .section-seven {
    width: 100%;
    background-color: #fff;
    padding: 60px 0;

    .container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      margin: auto;
      max-width: 1200px;

      .zh-banner {
        font-size: 35px;
        display: inline-block;
        font-weight: 500;
        color: #ee2c47;
        font-weight: bold;
      }

      .low-banner {
        font-size: 20px;
        margin: 10px;
        letter-spacing: 2px;
      }

      .introduction-container {
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        
        .list {
          flex: 0 0 140px;
          background: #fff;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: center;

          .icon {
            color: #e33858;
            font-size: 50px;
          }

          .context-title {
            font-size: 16px;
            font-weight: bold;
            margin: 10px 0;
            letter-spacing: 2px;
          }

          .context-content {
            font-size: 12px;
            letter-spacing: 2px;
          }
        }
      }
    }
  }
}

.vedio-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #333;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.8);
  z-index: 300;
  display: flex;
  align-items: center;
  justify-content: center;

  video {
    width: 880px;
    height: 495px;
    z-index: 301;
  }
  .close-btn {
    position: absolute;
    width: 38px;
    color: #000;
    z-index: 302;
    height: 38px;
    padding: 10px;
    display: flex;
    cursor: pointer;
    font-size: 35px;
    background: #fff;
    border-radius: 100px;
    top: calc(50% - 266px);
    left: calc(50% + 421px);
    align-items: center;
    justify-content: center;
  }
  @media (max-width: 600px) {
    video {
      width: 100%;
      height: 65vw;
    }
  }
}

@media (max-width: 600px) {
  .el-container {
    .section-one {
      height: calc(100vh - 55px);
      background-position-x: 75%;
      padding: 0;
      .zh-banner {
        font-size: 24px;
        letter-spacing: 1px;
        margin-top: 0;
      }
      .eng-banner {
        letter-spacing: 1px;
        font-size: 24px;
        margin: 0;
      }

      .btns {
        flex-direction: column;
        button {
          margin-bottom: 10px;
          font-size: 16px;
          strong {
            font-size: 20px;
          }
        }
      }
    }
    .section-two {
      flex-direction: column;
      align-items: center;
      margin-top: 20px;
      padding: 0;
      height: auto;
      .list {
        text-align: center;
        width: 90%;
        word-break: keep-all;
        .title {
          font-size: 24px;
        }
        .detail {
          font-size: 20px;
          word-break: inherit;
          line-height: 1.4em;
          margin-bottom: 10px;
        }
        .btn-color {
          margin: 20px 0;
          width: 130px;
          height: 40px;
        }
      }
      .br-left {
        display: none;
      }
      .list:last-child {
        border-left: 0;
        border-top: 2px solid #ccc;
      }
    }
    .section-three {
      padding: 0;
      margin: 0;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin: 20px auto;
      .container {
        .zh-banner {
          font-size: 17px;
          letter-spacing: 3px;
        }
        .eng-banner {
          font-size: 20px;
          letter-spacing: 3px;
          margin-top: 10px;
        }
        p.content {
          padding: 0 20px;
          color: #888;
          font-size: 13px;
          margin: 22px 0 10px;
        }

        .introduction-container {
          flex-direction: column;
          font-size: 13px;
          margin: 0;
          .list {
            height: auto;
            margin: 10px 20px;
            margin-top: 0px;
            box-sizing: border-box;
            text-align: center;
            .context {
              margin-top: 105px;
              padding: 20px;
              .context-title {
                text-align: center;
                margin: 0;
                padding: 10px 0;
                font-size: 16px;
              }
              .context-content {
                margin: 0;
              }
            }
            img {
              width: 100%;
            }
          }
        }
      }
    }
    .section-four {
      margin: 20px auto;
      width: 90%;
      padding: 0;
      height: auto;
      .zh-banner {
        font-size: 20px;
        letter-spacing: 3px;
        margin-top: 5px;
      }
      .eng-banner {
        font-size: 17px;
        letter-spacing: 0px;
      }
      .advance-container {
        flex-direction: column;
        flex-wrap: nowrap;
        margin: 0;
        .list {
          margin: 0;
          width: 100%;
          margin-top: 10px;
          height: auto;
          margin-bottom: 10px;
          flex-direction: column;
          .title {
            width: 100%;
            font-size: 16px;
            margin: 10px 0;
            svg.icon {
              margin-right: 5px;
              font-size: 20px;
              flex: 0 0 20px;
            }
          }
          .content {
            width: 100%;
            margin: 0;
            font-size: 13px;
          }
          img {
            width: 100%;
          }
        }
      }

      .frame-png {
        left: calc(50vw - 429px);
        @media (max-width: 600px) {
          overflow: hidden;
        }
      }
    }
    .section-five {
      margin-top: 20px;
      margin-bottom: 60px;
      .eng-banner.mobile {
        font-size: 20px;
      }
      .zh-banner {
        font-size: 17px;
        margin-top: 5px;
      }
      .contaienr {
        margin-top: 20px;
        flex-direction: column;
        .left {
          height: 64vw;
          margin: 10px 20px;
          background-size: cover;
        }
        .right {
          margin: 10px 20px;
          text-align: center;
          .title {
            font-size: 20px;
          }
          .main-content {
            font-size: 17px;
          }
          .btn-group {
            text-align: center;
            justify-content: center;
            margin-top: 20px;
          }
        }
      }
    }
    .section-six {
      .container {

        .zh-banner {
          font-size: 20px;
        }
        .introduction-container {
          flex-direction: row;
          justify-content: center;
          align-content: center;
          justify-content: space-between;
          padding: 0 15px;
          .list {
            flex: 0 0 43vw;
            width: 43vw;
            height: 43vw;
            margin: 10px 0;
            .icon {
              font-size: 58px;
              margin: 10px 3px 5px;
              font-weight: bold;
            }
            // .collaboration{
            //   font-size: 66px;
            // }
            img {
              margin: 4px 33px 0px;
            }
            p {
              text-align: center;
              word-break: break-word;
            }
            .context-title {
              font-size: 12px;
            }
          }
          .list:nth-child(3){
            .icon.edit {
              font-size: 63px;
            }
          }
        }
      }
    }
  }
}
</style>



