<template>
  <form v-on:submit.prevent class="container">
    <div class="list">
      <span>账户名: </span>
      <input autofocus v-model="name" name="name" type="text" placeholder="请输入您的姓名">
      <span></span>
    </div>
    <div class="list">
      <span>验证码: </span>
      <input autocomplete="off" v-model="verifyCode" name="verify-code" type="text" placeholder="请输入验证码">
      <span>
        <img @click="refreshImage" :src="codeImageSrc" alt="">
        看不清
        <a @click="refreshImage">换一张</a>
      </span>
    </div>
    <div class="list next">
      <span></span>
      <button :disabled="(name == '' || verifyCode=='')?true:false" @click="next" class="btn btn-primary">下一步</button>
      <span>
      </span>
    </div>
  </form>
</template>

<script>
import store from '@/store'
import axios from 'axios'
import router from '@/router'

export default {
  name: 'step1',
  data () {
    return {
      name: '',
      verifyCode: '',
      codeImageSrc: '/imageCode'
    }
  },
  methods: {
    next (e) {
      axios.post('/checkImageCode?imagecode=' + this.verifyCode, {
        params: {
          verifyCode: this.verifyCode
        }
      }).then(res => {
        if (res.data.success) {
          axios.get('/checkUsername?username=' + this.name, {
            body: {
              username: this.name
            }
          }).then(res => {
            if (res.data.success) {
              const changePasswordUserInfo = res.data.data
              store.commit('syncStateStr', {
                name: 'changePasswordUserInfo',
                val: changePasswordUserInfo
              })
              store.commit('syncStateStr', {
                name: 'passWDStep',
                val: Number(window.location.pathname.split('/')[window.location.pathname.split('/').length - 1]) + 1
              })
              router.push(`/modifypw/2`)
            } else {
              // 用户名无效
              commonStart.tip(res.data.msg, 'error')
            }
          })
        } else {
          // 验证码错误，刷新验证码
          commonStart.tip(res.data.msg, 'error')
          this.verifyCode = ''
          this.refreshImage()
        }
      })
    },
    refreshImage () {
      this.codeImageSrc = '/imageCode?' + Math.random()
    }
  },
  mounted () {
    store.commit('syncStateStr', {
      name: 'passWDStep',
      val: 2
    })
  }
}
</script>

<style lang="scss" scoped>
.container {
  margin: 120px 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .list {
    font-size: 14px;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    input {
      height: 75%;
      padding-left: 12px;
      flex: 0 0 250px;
      border-radius: 5px;
      border: 1px solid #ccc;
    }
    span {
      text-align: right;
      margin-right: 5px;
      display: flex;
      align-items: center;
      flex: 0 0 200px;
      justify-content: flex-end;

      img {
        cursor: pointer;
      }
      a {
        color: #11a0f8;
        word-spacing: 10px;
        user-select: none;
        cursor: pointer;
      }
    }
  }
  .list.next {
    margin-top: 20px;
  }
}
</style>



