<template>
  <span class="hr">
    <span class="line" :style="`background-color: ${color}`"></span>
    <span class="circle" :style="`color:${color}`">ㆍ</span>
    <span class="line" :style="`background-color: ${color}`"></span>
  </span>
</template>

<style lang="scss" scoped>
.hr {
  display: flex;
  flex-direction: row;
  align-items: center;
  .line {
    display: inline-block;
    width: 80px;
    height: 1px;
    background-color: #999;
  }
  .circle {
    font-size: 36px;
    color: #e84c3c;
    height: 0;
    line-height: 0;
  }
}
</style>

<script>
export default {
  name: 'v-Hr',
  data () {
    return {
      color: this.$attrs.color ? this.$attrs.color : ''
    }
  }
}
</script>



