import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import Header from './components/Header'
import Hr from './components/hr'
import Footer from './components/Footer'
import Swiper from './components/Swiper'
import Swiper1 from './components/Swiper1'
import Swiper1Mobile from './components/Swiper1Mobile'

import Swiper2 from './components/Swiper2'
import './styles/index.scss'
import './styles/globalnav.min.css'
import '@/icons/iconfont.js'
// vmei默认的一些弹出框组件
import './utils/common.js'
console.log('version: 1.0.2')

// 分栏
Vue.component(Hr.name, Hr)
// 导航栏组件
Vue.component(Header.name, Header)
// 底部组件
Vue.component(Footer.name, Footer)
// 轮播图组件
Vue.component(Swiper.name, Swiper)
// 轮播图组件1
Vue.component(Swiper1.name, Swiper1)

// 轮播图组件1 移动端
Vue.component(Swiper1Mobile.name, Swiper1Mobile)

// 轮播图组件2
Vue.component(Swiper2.name, Swiper2)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



