<template>
  <div class="swiper" :style="`height:${height}`">
    <!-- 👈按钮 -->
    <span class="turn" @click="debounceLeftMethod()">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-left"></use>
      </svg>
    </span>
    <!-- 图片容器 -->
    <div class="img-container">
      <div
        v-for="(item,i) in images"
        :key="i"
        :class="`img img${i + currentIndex > 2 ? i + currentIndex - 3 : i + currentIndex}`"
      >
        <img
          :src="item"
          :alt="`轮播图${i}`"
        >
      </div>
    </div>
    <!-- 👉按钮 -->
    <span class="turn" @click="debounceRightMethod()">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-right"></use>
      </svg>
    </span>

  </div>
</template>

<style lang="scss" scoped>
.swiper {
  user-select: none;
  width: 100%;
  top: 0;
  color: #fff;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  .turn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    color: #ccc;
    flex: 0 0 100px;
    cursor: pointer;
    margin-top: 100px;
    z-index: 1;
  }

  .img-container {
    height: 100%;
    flex: 0 0 80%;
    display: inline-flex;
    align-items: flex-end;
    position: relative;

    .img {
      position: absolute;
      left: calc(50% - 350px);
      transition: all 0.5s ease-in;
      &::after{
        left: 0;
        content: "";
        transition: all 0.5s ease-in;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.16), rgba(0, 0, 0, 0.16), rgba(0, 0, 0, 0)) no-repeat;
      }
      img {
        width: 700px;
      }
    }
    .img0 {
      transform: scale(0.9);
      left: -35px;
      z-index: 1;
      &::after{
        background-position-x: 50px;
      }
    }
    .img1 {
      z-index: 2;
      &::after{
        animation: img2 0.7s ease-in 0 1 normal forwards;
        background-position-x: 5000px;
      }
    }
    .img2 {
      transform: scale(0.9);
      left: calc(100% - 665px);
      z-index: 1;
      &::after{
        background-position-x: -50px;
      }
    }
  }
}

@keyframes img2 {
  from {
    background-position-x: 10px;
  }
  to {
    background-position-x: 300px;
  }
}
</style>

<script>
import throttle from 'lodash.throttle'
const dely = ms => new Promise(resolve => setTimeout(resolve, ms))
export default {
  name: 'v-Swiper2',
  data () {
    return {
      height: this.$attrs.height,
      images: this.$attrs.images,
      currentIndex: 0
    }
  },
  methods: {
    async plus () {
      if (this.currentIndex < 2) {
        this.currentIndex++
      } else {
        this.currentIndex = 0
      }
      await dely(3000)
    },
    debounceRightMethod: throttle(function () {
      if (this.currentIndex < 2) {
        this.currentIndex++
      } else {
        this.currentIndex = 0
      }
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.plus()
      }, 3000)
    }, 500),
    debounceLeftMethod: throttle(function () {
      if (this.currentIndex > 0) {
        this.currentIndex--
      } else {
        this.currentIndex = 2
      }
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.plus()
      }, 3000)
    }, 500)
  },
  mounted () {
    this.timer = setInterval(() => {
      this.plus()
    }, 3000)
  }
}
</script>



