import Vue from 'vue'
import Vuex from 'vuex'
// import user from './user'

Vue.use(Vuex)

export default new Vuex.Store({
  state () {
    return {
      user: {
        name: ''
      },
      globalId: '',
      // localStorage
      lang: localStorage.getItem('language') || 'zh',
      // passWDStep: 3,
      passWDStep: Number(location.pathname.split('/')[location.pathname.split('/').length - 1]),
      changePasswordUserInfo: {}
    }
  },
  mutations: {
    syncState: (state, data) => {
      state[data.name] = Object.assign({}, state[data.name], data.val)
    },
    syncStateStr: (state, data) => {
      state[data.name] = data.val
    }
  },
  strict: process.env.NODE_ENV !== 'production'
  // modules: {
  //   'user': user
  // },
})



