      bk: this.$attrs.bk,
      isMobile: window.screen.availWidth < 600
    }
  },
  mounted () {
    window.app = this
    window.store = store
  },
  methods: {
    changeLanguage (e) {
      if (e.target.dataset.lang) {
        store.commit('syncStateStr', {
          name: 'lang',
          val: e.target.dataset.lang
        })
        localStorage.setItem('language', e.target.dataset.lang)
      }
    }
  }
}
