<template>
  <div class="el-container login">
    <div class="title">
      <router-link to="/">
        <img src="https://assets.vmei.com/system/boss/boss-admin/img/boss-logov1.png" alt="logo">
      </router-link>
      <h1>供应商管理系统</h1>
    </div>
    <div class="container">
      <div class="content">
        <div class="title">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-info"></use>
          </svg>
          唯美不会以任何理由要求您转账汇款，谨防诈骗。
        </div>
        <form action="" v-on:submit.prevent="submitFunc">
          <span class="toggle-show-tip-info" v-if="tipInfo">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-jinzhi"></use>
            </svg>
            {{tipInfo}}
          </span>
          <p class="info">账号登录</p>
          <span class="container-input">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-user"></use>
            </svg>
            <input id="name" type="text" v-model="user.name" placeholder="邮箱/手机/用户名">
          </span>
          <span class="container-input">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-lock"></use>
            </svg>
            <input id="password" type="password" v-model="user.passWord" placeholder="密码">
          </span>
          <div class="feature">
            <router-link to="/register">商家注册</router-link>
            <router-link to="/modifypw/1">忘记密码</router-link>
          </div>
          <button class="login">登录</button>
        </form>
      </div>
    </div>
    <footer>
      <ul>
        <li><a href="https://www.vmei.com/help/74">关于我们</a></li>
        <!-- <li><a href="https://www.vmei.com/links">友情链接</a></li> -->
        <li><a href="https://www.vmei.com/help/500010">商家入驻</a></li>
        <!-- <li><a href="http://open.vmei.com/">开放平台</a></li> -->
        <!-- <li><a href="https://www.vmei.com/help/500007">加入我们</a></li> -->
        <li><a href="https://www.vmei.com/help/500002">商务合作</a></li>
        <li><a href="https://www.vmei.com/help/500001">隐私条例</a></li>
        <li><a href="https://www.vmei.com/help/500000">使用条款</a></li>
      </ul>
      <p class="copyright">Copyright © 2018香港思芙美有限公司. All rights reserved. 苏ICP备B2-20140092</p>
    </footer>
  </div>
</template>

<script>
// import store from '@/store'
// import router from '@/router'
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      user: {
        name: '',
        passWord: ''
      },
      tipInfo: ''
    }
  },
  methods: {
    isInfoShow () {
      if (this.tipInfo.length > 0) {
        setTimeout(() => {
          this.tipInfo = ''
          document.querySelector('.login #name').parentElement.style.border =
            ''
          document.querySelector(
            '.login #password'
          ).parentElement.style.border =
            ''
        }, 3000)
      }
    },
    submitFunc (e) {
      if (this.user.name === '' && this.user.passWord === '') {
        // 请输入用户名
        this.tipInfo = '请输入用户名和密码'
        document.querySelector('.login #name').parentElement.style.border = '1px solid #ff4400'
        document.querySelector('.login #name').focus()
        document.querySelector('.login #password').parentElement.style.border = '1px solid #ff4400'
        this.isInfoShow()
        return
      } else if (this.user.name === '') {
        this.tipInfo = '请输入用户名'
        document.querySelector('.login #name').parentElement.style.border = '1px solid #ff4400'
        document.querySelector('.login #name').focus()
        this.isInfoShow()
        return
      } else if (this.user.passWord === '') {
        this.tipInfo = '请输入密码'
        document.querySelector('.login #password').parentElement.style.border =
          '1px solid #ff4400'
        document.querySelector('.login #password').focus()
        this.isInfoShow()
        return
      }
      axios({
        method: 'post',
        url: '/do/login',
        params: {
          userName: this.user.name,
          password: this.user.passWord,
          redirectURL: '/supplychain/productPick'
        }
      })
        .then(res => {
          if (res.data.data && typeof res.data.data.backUrl === 'string') {
            this.isInfoShow()
            window.location.href =
              window.location.origin +
              decodeURIComponent(res.data.data.backUrl)
          } else {
            this.tipInfo = res.data.msg
            if (res.data.msg === '用户不存在') {
              document.querySelector('.login #name').parentElement.style.border = '1px solid #ff4400'
              document.querySelector('.login #name').focus()
              this.isInfoShow()
            } else if (res.data.msg === '密码错误') {
              document.querySelector('.login #password').parentElement.style.border = '1px solid #ff4400'
              document.querySelector('.login #password').focus()
              this.isInfoShow()
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-container {
  height: 100vh;
  width: 100vw;
  background-color: #f9f9f9;

  .title {
    width: 1000px;
    margin: auto;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    img {
      width: 100px;
    }
    h1 {
      color: #e93c4e;
      font-size: 24px;
      font-weight: normal;
      border-left: 1px solid #d4caca;
      padding: 10px 15px;
    }
  }
  .container {
    width: 100%;
    height: 60vh;
    background-image: url("https://assets.vmei.com/system/cosmos/images/newCosmos/background.png");
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;

    .content {
      margin-left: 600px;
      width: 360px;
      height: 390px;
      background-color: #fff;

      .title {
        height: 45px;
        width: 100%;
        font-size: 13px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: rgb(255, 248, 240);
        color: rgb(153, 153, 153);
        .icon {
          font-size: 20px;
          color: rgb(153, 153, 153);
          margin: 5px;
        }
      }
      form {
        position: relative;
        padding: 30px;
        display: flex;
        flex-direction: column;
        color: rgb(103, 103, 103);

        .toggle-show-tip-info {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
          top: 2px;
          left: 30px;
          padding: 4px 8px;
          position: absolute;
          width: calc(100% - 60px);
          border: 1px solid #ffafa2;
          background-color: #fef2f2;

          svg.icon {
            color: #ff4400;
          }
        }

        .info {
          font-size: 20px;
          letter-spacing: -2px;
          margin-bottom: 12px;
        }
        .container-input {
          margin: 10px 0;
          border: 1px solid #999;
          height: 45px;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          background: #f3f3f3;

          svg {
            color: #ababab;
            font-size: 20px;
            margin: 10px;
          }
          input {
            border: none;
            border-left: 1px solid #999;
            flex: 1;
            height: 100%;
            outline: none;
            padding: 14px;
            // color: #b5b5b5;
            background-color: #f3f3f3 !important;
            -webkit-box-shadow: 0 0 0px 1000px #fff inset;
          }
        }

        .feature {
          font-size: 14px;
          margin: 20px 0;
          display: inline-flex;
          align-items: center;
          justify-content: space-between;
        }
        button {
          cursor: pointer;
          background: #d84132;
          width: 100%;
          height: 45px;
          color: #fff;
          border: none;
          outline: none;
          letter-spacing: 1em;
        }
      }
    }
  }

  footer {
    width: 1000px;
    margin: auto;
    text-align: center;
    color: #333;

    ul {
      list-style-type: none;
      display: inline-flex;
      flex-direction: row;
      font-size: 14px;
      margin: 20px;

      li + li {
        border-left: 1px solid #333;
      }

      li {
        a {
          padding: 20px;
        }
      }
    }

    .copyright {
      font-size: 10px;
      text-align: center;
      color: #999;
    }
  }
}

@media (max-width: 600px) {
  .el-container {
    .title {
    }
    .container {
      .content {
        margin: 0;
        width: 100vw;
        height: 100%;
      }
    }
    footer {
      width: 100vw;
      ul {
        flex-wrap: wrap;
        flex-direction: row;
        li {
          flex: 0 0 100px;
          border: 1px solid rgba(0, 0, 0, 0) !important;
        }
      }
    }
  }
}
</style>



