<script>
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      screenWidth: screen.availWidth,
      companyData: {
        companyname: '',
        companyAddress: ''
      },
      contactsData: {
        businessName: '',
        businessEmail: '',
        businessPhone: '',
        wechat: ''
      }
    }
  },
  methods: {
    submitFunc (e) {
      axios({
        method: 'post',
        url: '/bussinessJoin/submit',
        params: {
          companyName: this.companyData.companyname,
          supplierType: 2,
          companyData: this.companyData,
          contactsData: this.contactsData
        }
      }).then(function (res) {
        if (res.data.success) {
          /* eslint-disable */
          window.commonStart.tip('已经登录您的信息，商务人员将尽快与您联系洽谈。', 'success');
        } else {
          console.log(res.data)
          /* eslint-disable */
          window.commonStart.tip('加入失败', 'warning');
        }
      }).catch(function (error) {
        console.log(error)
      })
    }
  }
}
