<template>
  <transition name="fade">
    <div class="container" @click="globalClickProxy">
      <router-view/>
    </div>
  </transition>
</template>

<style scoped>
  div.container {
    width: 100%;
    min-width: 1200px;
  }
  @media (max-width: 600px) {
    div.container {
      width: 100%;
      min-width: auto;
    }
  }

</style>

<script>
import store from '@/store'
export default {
  name: 'App',
  store,
  methods: {
    isDomFunc (dom, id) {
      // 递归到body元素就退出
      if (dom === document.body) return false
      if (dom.dataset.action && dom.dataset.action === id) {
        // 如果找到对应父元素就返回true
        return dom
      } else {
        // 负责递归父元素
        return this.isDomFunc(dom.parentElement, id)
      }
    },
    globalClickProxy (e) {
      const isDom = this.isDomFunc(e.target, 'toggleSlide')
      // console.log(
      //   isDom,
      //   store.state.globalId
      // );

      if (isDom && store.state.globalId !== 'toggleSlide') {
        store.commit('syncStateStr', {
          name: 'globalId',
          val: 'toggleSlide'
        })
      } else {
        store.commit('syncStateStr', {
          name: 'globalId',
          val: ''
        })
      }
    }
  }
}
</script>



