<template>
  <div class="step4-container">
    <svg class="icon" aria-hidden="true">
      <use xlink:href="#icon-ok"></use>
    </svg>
    <p>
      恭喜您！重置密码成功！
      <router-link to="/login">马上登录</router-link>
    </p>
  </div>
</template>

<script>
import store from '@/store'
export default {
  name: 'step4',
  mounted () {
    store.commit('syncStateStr', {
      name: 'passWDStep',
      val: Number(window.location.pathname.split('/')[window.location.pathname.split('/').length - 1])
    })
  }
}
</script>

<style lang="scss" scoped>
.step4-container {
  margin: 120px 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  svg.icon {
    font-size: 206px;
    color: #55ce63;
    margin-right: 3px;
  }
  a {
    color: #11a0f8;
    text-decoration: underline;
  }
}
</style>



