<script>
export default {
  name: 'index',
  data () {
    return {
      currentSwiperIndex: 1,
      vedioStart: false,
      global: 0,
      country: 0,
      user: 0,
      sku: 0
    }
  },
  methods: {
    animateInc (val, limit) {
      let incVal, ms;
      if (limit > 50) {
        incVal = limit / 200;
        ms = 17;
      } else {
        incVal = 1;
        ms = 190;
      }
      setTimeout(() => {
        if (this[val] < limit) {
          this[val] = this[val] + incVal;
          this.animateInc(val, limit);
        } else {
          this[val] = limit;
        }
      }, ms);
    }
  },
  mounted () {
    this.animateInc('sku', 1500);
    this.animateInc('user', 300);
    this.animateInc('global', 1500);
    this.animateInc('country', 20);
    window.scrollTo({
      top: 0
    });
  }
}
