<template>
  <form v-on:submit.prevent class="container">
    <div class="list phone">
      <span>手机号: </span>
      <span class="input">{{changePasswordUserInfo}}</span>
      <span class="tip">若该手机号已无法使用请联系客服</span>
    </div>
    <div class="list code">
      <span>请填写手机校验码: </span>
      <input class="input" ref='verifyCode' v-model="verifyCode" name="verify-code" type="text" placeholder="请输入验证码">
      <button @click="sendPhoneCode" class="btn btn-primary" id="getMsgCode">
        获取短信验证码
      </button>
    </div>
    <div class="list next">
      <span></span>
      <button :disabled="verifyCode==''?true:false" @click="next" class="btn btn-primary">下一步</button>
      <span></span>
    </div>
  </form>
</template>

<script>
import store from '@/store'
import router from '@/router'
import axios from 'axios'

export default {
  name: 'step2',
  data () {
    return {
      verifyCode: '',
      userInfo: store.state.changePasswordUserInfo,
      reduceTime: 120
    }
  },
  computed: {
    changePasswordUserInfo () {
      if (this.userInfo) {
        return this.userInfo.codephone
      } else {
        return '138*****123'
      }
    }
  },
  methods: {
    sendPhoneCode (e) {
      if (/^[\d|*]{11}$/.test(this.userInfo.user.phone)) {
        // 如果有11位数字的
        axios.get('/setting/checkcode?phone=' + this.userInfo.user.phone).then(res => {
          if (res.data.success) {
            commonStart.tip(res.data.msg, 'success')
            // 将按钮变成倒计时
            const btn = document.querySelector('#getMsgCode')
            btn.disabled = true
            btn.innerHTML = this.reduceTime + 's'
            const reduceFunc = () => {
              const num = Number(btn.innerHTML.replace(/\D/, ''))
              if (num > 0) {
                setTimeout(() => {
                  btn.innerHTML = (num - 1) + 's'
                  reduceFunc()
                }, 1000)
              } else {
                btn.innerHTML = '获取短信验证码'
                btn.disabled = false
              }
            }
            reduceFunc()
            // document.querySelector('[name="verify-code"]')
            // this.$refs.verifyCode.$el.focus()
          } else {
            commonStart.tip(res.data.msg, 'error')
          }
        })
      } else {
        commonStart.tip('手机号码格式错误', 'error')
      }
    },
    next (e) {
      axios.get('/verify/code', {
        params: {
          checkCode: this.verifyCode,
          phone: this.userInfo.user.phone
        }
      }).then(res => {
        console.log(res.data)
        if (res.data.success) {
          store.commit('syncStateStr', {
            name: 'passWDStep',
            val: 3
          })
          console.log(res.data.msg)
          router.push(`/modifypw/3`)
        } else {
          commonStart.tip(res.data.msg, 'error')
        }
      })
    }
  },
  mounted () {
    window.app = this
    store.commit('syncStateStr', {
      name: 'passWDStep',
      val: Number(
        location.pathname.split('/')[location.pathname.split('/').length - 1]
      )
    })
  }
}
</script>

<style lang="scss" scoped>
.container {
  margin: 120px 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .list {
    font-size: 14px;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    input {
      height: 75%;
      padding-left: 12px;
      flex: 0 0 250px;
      border-radius: 5px;
      border: 1px solid #ccc;
    }
    span {
      text-align: right;
      margin-right: 5px;
      display: flex;
      align-items: center;
      flex: 0 0 180px;
      justify-content: flex-end;

      a {
        color: #11a0f8;
        word-spacing: 10px;
      }
    }
  }
  .list.phone {
    .input {
      height: 75%;
      flex: 0 0 100px;
      color: #ec4165;
      font-weight: bold;
      letter-spacing: 1px;
      font-size: 20px;
      word-break: keep-all;
    }
    .tip {
      color: #b3b3b3;
      flex: 0 0 300px;
      justify-content: flex-start;
    }
  }
  .list.code {
    .btn {
      margin-left: 10px;
      width: 120px;
      padding: 10px 0px;
    }
  }
  .list.next {
    margin-top: 20px;
  }
}
</style>



