<template>
  <div class="swiper" :style="`height:${height}`">
    <!-- 图片容器 -->
    <div class="img-container">
      <div
        v-for="(images,i) in container"
        :key="i"
        :class="`container container${i + currentIndex > 2 ? i + currentIndex - 3 : i + currentIndex}`"
      >
        <span v-for="(item,i) in images" :key="i" class="list">
          <img
            :src="item.img"
            :alt="`轮播图${i}`"
          />
          <p v-for="(p,i) in item.text.split('\n')" :key="i">{{p}}</p>
        </span>
      </div>
    </div>
    <!-- 导航点 -->
    <div class="nav">
      <span
        @click="tapIndex(i)"
        v-for="(e,i) in container"
        :class="`dot ${(2-i)===currentIndex ? 'active': ''}`"
        :key="i"
      ></span>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.swiper {
  user-select: none;
  width: 100%;
  top: 0;
  color: #fff;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .img-container {
    height: 100%;
    flex: 0 1 1400px;
    max-width: 1400px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    position: relative;
    overflow-x: hidden;

    .container {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-around;
      transition: all 0.5s ease-in;
      position: absolute;
      left: 0;
      width: 100%;
      color: #000;
      font-size: 13px;
      color: #333;
      line-height: 2em;
      img {
        margin-right: 30px;
      }
    }
    .container0 {
      transform: translateX(-100%);
      z-index: -1;
    }
    .container1 {
      z-index: 2;
      transform: translateX(0);
      background: inherit;
    }
    .container2 {
      transform: translateX(100%);
      z-index: -1;
    }
  }

  .nav {
    display: flex;
    flex-direction: row;

    .dot {
      cursor: pointer;
      display: inline-block;
      width: 10px;
      margin: 10px;
      height: 10px;
      border-radius: 100px;
      background: #b1b1b161;
      &.active {
        background: red;
      }
    }
  }
}

</style>

<script>
const dely = ms => new Promise(resolve => setTimeout(resolve, ms))
export default {
  name: 'v-Swiper1',
  data () {
    return {
      height: this.$attrs.height,
      container: this.$attrs.container,
      currentIndex: 2
    }
  },
  methods: {
    async reduce () {
      if (this.currentIndex > 0) {
        this.currentIndex--
      } else {
        this.currentIndex = 2
      }
      await dely(3000)
    },
    tapIndex (i) {
      clearInterval(window.timer);
      window.timer = setInterval(() => {
        this.reduce()
      }, 3000)
      this.currentIndex = 2 - i;
    }
  },
  mounted () {
    window.timer = setInterval(() => {
      this.reduce()
    }, 3000)
  }
}
</script>



