<template>
  <div class="el-container">
    <!-- 头部信息 -->
    <v-Header bk="white"></v-Header>
    <p class="breadCrumbs">
      <router-link to="/">首页</router-link>
       >
      <router-link to="/login">商家登录</router-link>
       >
      <router-link :to="`/modifypw/${$store.state.passWDStep}`">忘记密码</router-link>
    </p>
    <div class="contianer">
      <div class="direct">
        <span class="step active">
          <svg v-if="$store.state.passWDStep >1" class="icon" aria-hidden="true">
            <use xlink:href="#icon-duigou"></use>
          </svg>
          <span v-else>①</span>
        </span>
        <span class="mid active"></span>
        <span :class="`step ${$store.state.passWDStep > 1 ? 'active':''}`">
          <svg v-if="$store.state.passWDStep > 2" class="icon" aria-hidden="true">
            <use xlink:href="#icon-duigou"></use>
          </svg>
          <span v-else>②</span>
        </span>
        <span :class="`mid ${$store.state.passWDStep > 2 ? 'active':''}`"></span>
        <span :class="`step ${$store.state.passWDStep > 2 ? 'active':''}`">
          <svg v-if="$store.state.passWDStep > 3" class="icon" aria-hidden="true">
            <use xlink:href="#icon-duigou"></use>
          </svg>
          <span v-else>③</span>
        </span>
        <span :class="`mid ${$store.state.passWDStep > 2 ? 'active':''}`"></span>
        <span :class="`step ${$store.state.passWDStep > 3 ? 'active':''}`">
          <svg v-if="$store.state.passWDStep > 3" class="icon" aria-hidden="true">
            <use xlink:href="#icon-duigou"></use>
          </svg>
          <span v-else>④</span>
        </span>
      </div>
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'modifyPassword'
}
</script>

<style lang="scss" scoped>
.el-container {
  margin: auto;

  .breadCrumbs {
    margin: 10px auto;
    max-width: 1200px;
    width: 100%;
    font-size: 12px;
    color: #333;
  }
  .contianer {
    max-width: 1200px;
    width: 100%;
    margin: 10px auto;
    border-top: 4px solid #ec4165;
    box-shadow: 0px 0px 5px #ccc;
    height: calc(100vh - 280px);
    padding: 75px 130px;

    .direct {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 30px;
      justify-content: center;

      .mid {
        width: 240px;
        border-bottom: 3px solid #9b9b9b;
        &.active {
          border-bottom: 3px solid #ec4165;
        }
      }
      .step {
        line-height: 100%;
        color: #9b9b9b;
        position: relative;
        &.active {
          color: #ec4165;
        }
        &:after {
          letter-spacing: 1px;
          bottom: -27px;
          width: 100px;
          left: 0px;
          font-size: 14px;
          font-weight: bold;
          color: inherit;
          position: absolute;
        }
        &:first-child:after {
          content: '填写用户名';
        }
        &:nth-child(3):after {
          content: '验证身份';
        }
        &:nth-child(5):after {
          content: '重置密码';
        }
        &:last-child:after {
          content: '重置成功';
        }

        span {
          transform: translateY(-10%);
        }
      }
    }
  }
}
</style>



