import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/views/404'
import Index from '@/views/Index.vue'
import Login from '@/views/login'
import Register from '@/views/register'
import JoinRequest from '@/views/joinRequest'
import modifyPW from '@/views/modifyPW'
import modifyPW1 from '@/views/modifyPW/step1'
import modifyPW2 from '@/views/modifyPW/step2'
import modifyPW3 from '@/views/modifyPW/step3'
import modifyPW4 from '@/views/modifyPW/step4'
import CustmerJoinRequest from '@/views/CustmerJoinRequest'
import FreeTry from '@/views/freeTry'
import TalentJoin from '@/views/talentJoin'
// import Detail from '../detail'
// import PublishGood from '@/views/main/publishGood'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    component: Index,
    name: '管理首页',
    hidden: true
  }, {
    path: '/login',
    component: Login,
    name: '登录页面',
    hidden: true
  }, {
    path: '/register',
    component: Register,
    name: '登录页面',
    hidden: true
  }, {
    path: '/joinrequest',
    component: JoinRequest,
    name: '品牌入驻申请',
    hidden: true
  }, {
    path: '/custmerJoinRequest',
    component: CustmerJoinRequest,
    name: '唯美招商入驻要求',
    hidden: true
  }, {
    path: '/modifypw',
    component: modifyPW,
    name: '修改密码',
    hidden: true,
    children: [{
      path: `/modifypw/1`,
      component: modifyPW1
    }, {
      path: `/modifypw/2`,
      component: modifyPW2
    }, {
      path: `/modifypw/3`,
      component: modifyPW3
    }, {
      path: `/modifypw/4`,
      component: modifyPW4
    }]
  }, {
    path: '/freetry',
    component: FreeTry,
    hidden: true
  }, {
    path: '/talentjoin',
    component: TalentJoin,
    hidden: true
  }, {
    path: '*',
    component: NotFound,
    name: '未找到',
    hidden: true
  }],
  mode: 'history',
  linkActiveClass: 'active',
  linkExactActiveClass: 'active'
})

const limitRouterFunc = ({ to, next, limitPage }) => {
  if (limitPage.indexOf(to.path) >= 0) {
    next()
  } else {
    console.log('然后你要登录呢？')
    next({ path: limitPage[0] })
  }
}

router.beforeEach((to, from, next) => {
  // const isLogin = store.state.user.name.length > 0
  const isLogin = true
  if (!isLogin) {
    limitRouterFunc({
      to,
      next,
      limitPage: [`/login`, `/register`, '/', '/detail', '/custmerJoinRequest']
    })
  } else {
    next()
  }
})

export default router



