<template>
  <form v-on:submit.prevent class="container">
    <div class="list tip">
      <span class="left"></span>
      <span class="right">
        <span class="info">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-ok"></use>
          </svg>
          验证已通过，您可以设置新的密码！
        </span>
        <span class="tip"></span>
      </span>
    </div>
    <div class="list newPW">
      <span class="left">新密码: </span>
      <span class="right">
        <input autocomplete="off" maxlength="20" v-model="passWD" class="input" name="password" type="password" placeholder="请输入验证码">
        <span class="tip">请填写8-20位，大小写英文</span>
        <!-- <span class="tip">请填写10-20位，大小写英文，并至少有一位特殊字符(-=!&@#$%,.+)</span> -->
      </span>
    </div>
    <div class="list">
      <span class="left">确认原密码: </span>
      <span class="right">
        <input autocomplete="off" maxlength="20" v-model="newPassWD" class="input" name="confirm-password" type="password" placeholder="请输入验证码">
        <span class="tip"></span>
      </span>
    </div>
    <div class="list next">
      <span></span>
      <button :disabled="(passWD == '' || newPassWD=='')?true:false" @click="next" class="btn btn-primary">下一步</button>
      <span></span>
    </div>
  </form>
</template>

<script>
import axios from 'axios'
import store from '@/store'
import router from '@/router'

export default {
  name: 'step2',
  data () {
    return {
      passWD: '',
      newPassWD: ''
    }
  },
  methods: {
    next () {
      if (this.passWD !== this.newPassWD) {
        commonStart.tip('两次密码不一致', 'error')
        return
      }
      if (this.passWD.length < 8) {
        commonStart.tip('请填写8-20位', 'error')
        return
      }
      if (
        this.passWD.match(/[a-z]/) == null
      ) {
        commonStart.tip('没有大小写英文', 'error')
        return
      }
      if (
        this.passWD.match(/[A-Z]/) == null
      ) {
        commonStart.tip('没有大小写英文', 'error')
        return
      }
      axios({
        url: '/user/forgotPwd',
        method: 'POST',
        headers: {
          // 'Content-Type': 'application/x-www-form-urlencoded'
          'Content-Type': 'application/json'
        },
        data: {
          username: store.state.changePasswordUserInfo.user.userName,
          newPassword: this.passWD
        }
      }).then(res => {
        if (res.data.success) {
          store.commit('syncStateStr', {
            name: 'passWDStep',
            val: 4
          })
          router.push(`/modifypw/4`)
        } else {
          commonStart.tip(res.data.msg, 'error')
        }
      })
    }
  },
  mounted () {
    store.commit('syncStateStr', {
      name: 'passWDStep',
      val: Number(window.location.pathname.split('/')[window.location.pathname.split('/').length - 1])
    })
  }
}
</script>

<style lang="scss" scoped>
.container {
  margin: 70px 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .list {
    margin-bottom: 10px;
    font-size: 14px;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    .left {
      line-height: 37.5px;
      text-align: right;
      margin-right: 5px;
      display: flex;
      align-items: center;
      flex: 0 0 180px;
      justify-content: flex-end;
      a {
        color: #11a0f8;
        word-spacing: 10px;
      }
    }
    .right {
      flex: 0 0 450px;
      height: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;
      .tip {
        color: #888;
      }
      .input {
        height: 75%;
        width: 250px;
        padding-left: 12px;
        border-radius: 5px;
        border: 1px solid #ccc;
      }
    }
  }

  .list.tip {
    .info {
      display: inline-block;
      flex: 0 0 250px;
      height: 30px;
      padding: 10px;
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;
      font-size: 12px;
      text-align: left;
      background-color: #fffff0;
      border: 1px solid #ffcc66;
      svg.icon {
        font-size: 16px;
        color: #85cc57;
        margin-right: 3px;
      }
    }
  }
  .list.newPW {
    margin-bottom: 20px;
    line-height: 30px;
    .btn {
      margin-left: 10px;
      width: 120px;
      padding: 10px 0px;
    }
    .right {
      .tip {
        height: 37.5px;
        line-height: 37.5px;
        padding: 0 10px;
      }
    }
  }
  .list.next {
    margin-top: 20px;
    span {
      width: 185px;
    }
  }
}
</style>



